/***************************************
01_reset.less
***************************************/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
input {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}
p,
blockquote {
  font-weight: normal;
}
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  background: transparent;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
:focus {
  outline: none;
}
ins {
  text-decoration: none;
}
del {
  text-decoration: line-through;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input,
textarea,
select,
button {
  /* disable default webkit form styles */
  -webkit-appearance: none;
  border-radius: 0;
  /* removes whitespace around forms */
  margin: 0;
  vertical-align: middle;
}
button {
  margin: 0;
  padding: 0;
  background: none;
  border: none;
}
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}
/* Removes outline from FF*/
button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}
select {
  -moz-appearance: none;
  text-indent: 0.01px;
  text-overflow: '';
}
select::-ms-expand {
  display: none;
}
input[type='number'] {
  -moz-appearance: textfield;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
/***************************************
02_base.less
***************************************/
html,
body {
  min-width: 320px;
}
html {
  background: #fff;
  font-size: 16px;
}
body {
  padding: 24px;
}
body,
button,
input,
select,
textarea {
  color: #282828;
  font-family: 'Nunito', sans-serif;
  font-size: 16px;
  line-height: 18px;
  -webkit-text-size-adjust: none;
  font-smooth: always;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/***************************************
03_type.less
***************************************/
h1,
h2 {
  color: #282828;
}
h1 {
  margin-bottom: 12px;
  font-size: 30px;
  font-size: 1.875rem;
  line-height: 32px;
  line-height: 2rem;
}
h2 {
  margin-bottom: 12px;
  font-size: 21px;
  font-size: 1.3125rem;
  line-height: 26px;
  line-height: 1.625rem;
}
h3 {
  margin-bottom: 12px;
  font-size: 21px;
  font-size: 1.3125rem;
  line-height: 26px;
  line-height: 1.625rem;
  font-weight: 400;
}
p {
  margin-bottom: 18px;
  font-size: 16px;
  font-size: 1rem;
  line-height: 20px;
  line-height: 1.25rem;
}
dl dt {
  margin-top: 6px;
  font-weight: bold;
}
dl dt:first-child {
  margin-top: 0;
}
dl dd {
  margin-left: 18px;
}
a {
  color: #282828;
  text-decoration: underline;
}
a:hover,
a:focus {
  text-decoration: none;
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
/***************************************
04_forms.less
***************************************/
@media (min-width: 30em) {
  .form__row {
    display: flex;
  }
}
label {
  display: block;
  margin-bottom: 6px;
  font-size: 16px;
  font-size: 1rem;
  line-height: 16px;
  line-height: 1rem;
  font-weight: 700;
}
.input {
  position: relative;
}
@media (min-width: 30em) {
  .form__row .input {
    display: flex;
    flex-grow: 1;
    margin-right: 18px;
  }
}
.input__addon {
  position: absolute;
  top: 50%;
  transform: translate(12px, -50%);
  font-size: 16px;
  font-size: 1rem;
  line-height: 16px;
  line-height: 1rem;
  font-weight: 700;
}
.input__addon + .form__control {
  padding-left: 24px;
}
.form__control {
  width: 100%;
  padding: 12px;
  background-color: #fff;
  border: 1px solid #9f9f9f;
  color: #282828;
  font-size: 16px;
  font-size: 1rem;
  line-height: 16px;
  line-height: 1rem;
  transition: box-shadow 0.2s ease-in-out, border-color 0.2s ease-in-out;
}
@media (min-width: 30em) {
  .form__control {
    flex-grow: 1;
  }
}
.form__control:hover {
  border-color: currentColor;
}
.form__control:focus {
  border-color: #334c89;
  box-shadow: 0 0 3px #334c89;
}
