/* clear */
.row:after {
  content: "";
  display: table;
  clear: both;
}
[class*='col--'] {
  padding: 0 12px;
}
.row .row {
  margin-right: -12px;
  margin-left: -12px;
}
/***************************************
05_elements.less
***************************************/
.btn {
  padding: 18px;
  background: #334c89;
  border-radius: 4px;
  color: #fff;
  font-size: 16px;
  font-size: 1rem;
  line-height: 16px;
  line-height: 1rem;
  font-weight: 700;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}
@media (max-width: 29.9375em) {
  .btn {
    display: block;
    width: 100%;
    border-radius: 0 0 4px 4px;
  }
}
.btn:hover,
.btn:focus {
  background: rgba(144, 157, 49, 0.75);
}
.progress {
  appearance: none;
  width: 100%;
  height: 24px;
  margin-bottom: 30px;
  border: 1px solid #497b10;
  background-color: transparent;
  color: #909d31;
}
.progress::-webkit-progress-bar {
  background-color: transparent;
}
.progress::-webkit-progress-value {
  background-color: #909d31;
}
.progress::-moz-progress-bar {
  background-color: #909d31;
}
.table table {
  width: 100%;
  margin-bottom: 24px;
}
.table th,
.table td {
  padding-right: 12px;
}
.table th {
  padding-bottom: 12px;
  font-size: 20px;
  font-size: 1.25rem;
  line-height: 20px;
  line-height: 1.25rem;
  font-weight: 400;
  text-align: left;
}
@media (max-width: 29.9375em) {
  .table th {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
  }
}
.table td {
  font-size: 30px;
  font-size: 1.875rem;
  line-height: 30px;
  line-height: 1.875rem;
  font-weight: 700;
}
@media (max-width: 29.9375em) {
  .table td {
    display: block;
  }
}
@media (max-width: 29.9375em) {
  .table td:before {
    content: attr(data-th);
    display: block;
    margin-bottom: 6px;
    font-size: 20px;
    font-size: 1.25rem;
    line-height: 20px;
    line-height: 1.25rem;
    font-weight: 400;
  }
}
.table td + td {
  margin-top: 18px;
}
/***************************************
06_components.less
***************************************/
.logo {
  width: 68px;
  margin-right: 18px;
}
@media (min-width: 30em) {
  .title {
    display: flex;
    align-items: center;
  }
}
.donation {
  max-width: 412px;
  background-color: #fafafa;
  border: 1px solid #dcdcdc;
  box-shadow: 0 4px 2px -2px #dcdcdc;
  transition: box-shadow 0.2s ease-in-out;
}
.donation__main {
  padding: 30px 30px;
}
.donation__header {
  margin-bottom: 30px;
}
.donation__footer {
  padding: 24px 30px;
  border-top: 1px solid #dcdcdc;
}
.donation__footer > :last-child {
  margin-bottom: 0;
}
#progress,
#figures {
  display: none;
}
#progress.show,
#figures.show {
  display: block;
}
.achieved {
  color: #497b10;
}
.success {
  padding: 12px;
  background-color: #909d31;
  color: #fff;
  font-weight: 700;
}
